button {
  width: 200px;
  height: 150px;
  cursor: pointer;
  border: 2px solid beige;
  border-radius: 8px;
  margin-top: 15%;
  padding: 12px 28px;
  font-size: 1.4vw;
  transition: background-color .5s linear, box-shadow .5s linear;
  position: relative;
  left: 50%;
  transform: translate(-50%);
}

button:hover, button:focus {
  background-color: beige;
  box-shadow: 0 12px 16px rgba(0, 0, 0, .24), 0 17px 50px rgba(0, 0, 0, .19);
}

